.split_container {
  width: 100%;
  min-width: 0;
  min-height: 100vh;
  display: flex;
  justify-content: space-evenly;
  color: white;
}

.flex_child {
  flex-basis: 49%;
  min-width: 0;
}

.dropdown {
  padding: 20px;
}

@media (max-width: 1000px) {
  .split_container {
    display: block;
  }
}